.code-preview {
  position: relative;
  border: 1px solid #f3f3f3;
  border-radius: 4px;

  .actions {
    color: #999;
    justify-content: flex-end;
    align-items: center;
    display: flex;
    border-bottom: 1px dashed #f3f3f3;
    padding-right: 12px;

    button {
      color: #999;
      align-items: center;
      display: flex;
      transition: color 200ms;
      padding: 0 6px;

      &:active {
        border-color: transparent;
        background-color: transparent;
      }

      &:hover {
        color: #333;
      }
    }
  }
}

.clipboard-content {
  position: absolute;
  top: -999em;
  left: -999em;
  z-index: 999;
}

.markdown {
  line-height: 2;
  font-size: 16px;
  color: #1f2d3d;

  a {
    transition: color 0.3s ease;
    color: #00bc70;

    &:hover {
      color: #00d17d;
    }
  }

  .grid-container {
    overflow-x: auto;
    margin: 20px 0;
  }

  ul {
    margin-left: 0;
    padding-left: 0;

    > li {
      margin-left: 20px;
      padding-left: 4px;
      list-style-type: circle;
    }
  }

  pre {
    margin: 0;
    padding: 1em;
    overflow: auto;
    font-size: 14px;
    line-height: 1.5;
    background-color: #f6f8fa;
    border-radius: 3px;
  }

  h1,
  h2,
  h3,
  h4 {
    font-weight: 500;
  }

  blockquote {
    margin: 0;
    padding: 0 20px;
    color: #6a737d;
    border-left: 4px solid #dfe2e5;
  }

  code {
    font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
    padding: 0.2em 0.4em;
    margin: 0;
    font-size: 85%;
    background-color: rgba(175, 184, 193, 0.2);
    border-radius: 6px;
  }

  table.grid {
    font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
    width: 100%;
    min-width: 600px;
    font-size: 14px;
    border-collapse: collapse;
    border-spacing: 0;

    strong {
      font-weight: normal;
    }

    th {
      text-align: left;
      border-top: 1px solid #eaeefb;
      background-color: #f9fafc;
      white-space: nowrap;
      font-weight: normal;
      color: #999;
    }

    td,
    th {
      border-bottom: 1px solid #eaeefb;
      padding: 10px;
      max-width: 250px;
    }

    th:first-child,
    td:first-child {
      padding-left: 15px;
    }
  }
}
